  },
  firestore: {
    documents: db.collection("name")
  },
  created: function() {
    // this.user = db.collection("documents")
    
  },

  methods: {
    save() {
      var userdata = {};
      userdata = this.user;

      db.collection("name")
        .doc(this.user)
        .set(userdata)
        .then(() => {
          // console.log("user updated!");
        });
      // we can also use `$firestoreRefs.user` to refer to the bound user reference
      this.$firestoreRefs.userdata.set(userdata);
   
    }
  }
};
</script>

